.App-container {
  margin: 0 auto;
  max-width: 1200px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

h2 {
  margin: 0 0 30px 0;
  padding: 0;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}

h3 {
  margin: 0 0 20px 0;
  padding: 0;
}

button {
  width: 200px;
  height: 50px;

  font-weight: bold;

  background-color: transparent;
  text-transform: uppercase;
  font-size: 20px;

  border: 2px solid black;
}

input,
textarea {
  padding: 5px;
  margin-right: 20px;

  width: 100%;

  background-color: black;
  border: none;
  border-bottom: 1px solid #8e8e8e;
  outline: none;
  color: white;
}

input::placeholder,
textarea::placeholder {
  color: #8e8e8e;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
