.header {
  padding: 10px 20px;
  /*padding-left: 20px;
    padding-right: 20px;*/

  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;

  background-color: black;
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 600px) {
  .header {
    height: 60px;
    padding: 0 20px;

    justify-content: start;
  }
}
