.menu_list {
  margin: 0;
  padding: 0;

  list-style: none;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.menu_item {
  margin-right: 30px;
}

.menu_item:last-child {
  margin-right: 0;
}

.item_link {
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
}

.item_link:hover {
  color: #e0040b;
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 600px) {
  .item_link {
    font-size: 12px;
    text-transform: uppercase;
  }
}
