.works {
  padding: 100px 0;
  margin-left: 10px;
  width: 100%;
  height: 100%;
}

.works_list {
  margin: 0;
  padding: 0;

  list-style: none;

  display: grid;
  grid-template-columns: repeat(auto-fill, 370px); /* или repeat(auto-fit, 100px); */
  grid-gap: 45px;
}

@media only screen and (max-width: 600px) {
  .works {
    padding: 30px 0;
  }

  .works_list {
    width: 100%;

    flex-direction: column;
  }
}

@media only screen and (max-width: 1062px) {
  .works_list {
    justify-content: space-evenly;
  }
}
